body {
  line-height: 1.6;
  color: #6b7885;
  overflow-x: hidden;
  font-size: 0.84rem;
  scroll-behavior: smooth;
  font-family: "Inter", sans-serif;
  transition: all 0.3s ease;
  -webkit-font-smoothing: antialiased;
  background-color: rgb(243, 244, 246);
}

*:focus {
  outline: none;
  text-decoration: none;
}

a {
  color: #283c50;
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
}
a:hover {
  color: #3454d1;
  text-decoration: none;
}
a:not([href]) {
  color: inherit;
}

b,
strong {
  font-weight: 600;
}

.c-pointer {
  cursor: pointer;
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  color: #283c50;
  font-weight: 700;
}

img {
  border-radius: 3px;
}

