<--!----------------------------------------------------------------!-->
*/
.avatar-text,
.avatar-image {
  width: 40px;
  height: 40px;
  max-width: 40px;
  max-height: 40px;
  min-width: 40px;
  min-height: 40px;
  overflow: hidden;
  border-radius: 100%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  background-color: #ffffff;
  border: 1px solid #dcdee4;
}

