  color: #283c50;
  font-size: 12px;
  font-weight: 500;
  position: relative;
  display: inline-block;
  width: 2.5em;
  height: 2.5em;
  line-height: 2.5em;
  border-radius: 50%;
  border: 2px solid transparent;
  cursor: pointer;
}
.calendar .day:hover {
  border: 2px solid #3454d1;
}
