  color: #3454d1;
  background: #eff0f6;
}
.calendar .day.has-event:after {
  content: "";
  position: absolute;
  top: calc(50% + 0.6em);
  left: calc(50% - 2px);
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background: #3454d1;
}
.calendar .day.disabled {
  cursor: default;
}
.calendar .day.disabled:hover {
  border: 2px solid transparent;
}
