  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  width: 2px;
  height: 100%;
  background-color: #ea4d4d;
}
.calendar .event-container .close:before {
  transform: rotate(45deg);
}
.calendar .event-container .close:after {
  transform: rotate(-45deg);
}
